.now-prom .trading-mining .trading .trading-top .trading-top-list {
  .list {
    .list-title {
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: #1b1c22;
      position: relative;

      .top-points,
      .top-pnl {
        width: 390px;
        height: 70px;
        line-height: 50px;
        text-align: center;
        border-radius: 20px 20px 0 0px;
        background-color: #202124;
        cursor: pointer;
      }

      .cur-pnl,
      .cur-points {
        background-color: #0D0E0F;
      }
    }

    .list-box {
      border: 1px solid #0D0E0F;
      margin-top: -25px;
      z-index: 9;
      position: relative;
      height: 888px;

      .pnl-list-info {
        text-align: center;
        // line-height: 890px;
      }

      .list-info {
        .progress {
          width: 380px;
          left: 180px;
          top: 75px;

          .one-points-progress,
          .one-pnl-progress {
            width: 100%;
          }
        }

        .pnl-progress {
          width: 300px;
          left: 160px;
          top: 75px;
        }
      }
    }

    // .your-rewards {
    //   .raise-score {
    //     border: 0;
    //     padding: 0;
    //   }
    // }
  }

}